import React, { Fragment, useState, useEffect } from "react";
import { Button, Modal, Form, Input, InputNumber } from "antd";
import { QuestionCircleOutlined } from "@ant-design/icons";

import $ from "jquery";
import Subject from "./subject";
import axiosInstance from "../../../actions/axios";
import { updateTitle } from "../../common/UpdateTitle";

const { confirm, success } = Modal;

const MotherSet = (props) => {
  const [motherset, setMotherset] = useState([]);
  const [form] = Form.useForm();
  const [selMSetID, setSelMSetID] = useState(0);

  useEffect(() => {
    setMotherset([]);

    axiosInstance.get("/set/mother/").then((res) => {
      setMotherset(res.data);
    });
    updateTitle({
      title: "MotherSet",
    });
  }, [setMotherset]);

  const addMotherSet = (e) => {
    const motherBody = JSON.stringify({
      name: e.mother,
      description: e.mother_des,
    });

    axiosInstance
      .post("/set/mother/", motherBody)
      .then((res) => {
        setMotherset([...motherset, res.data]);
        form.resetFields();
        setSelMSetID(0);
        Modal.success({
          title: "Success",
          content: (
            <Fragment>
              <h5>Name: {res.data.name}</h5>
              <p>Description: {res.data.description}</p>
            </Fragment>
          ),
        });
      })
      .catch(() => {
        Modal.error({
          title: "Error",
          content: "Something Went wrong, Refresh the Page and try again",
        });
      });
  };
  const updateMotherSet = (val) => {
    const motherBody = JSON.stringify({
      name: val.mother1,
      description: val.mother_des1,
    });

    axiosInstance
      .patch(`/set/mother/${val.id}/`, motherBody)
      .then((res) => {
        const objIndex = motherset.findIndex((obj) => obj.id === res.data.id);
        motherset[objIndex] = res.data;
        setMotherset([...motherset]);
        Modal.success({
          title: "Success",
          content: (
            <Fragment>
              <h5>Name: {res.data.name}</h5>
              <p>Description: {res.data.description}</p>
            </Fragment>
          ),
        });
        setTimeout(() => Modal.destroyAll(), 1000);
      })
      .catch((err) => {
        Modal.error({
          title: "Error",
          content:
            err.response.data.detail ||
            err.response.data.name.join() ||
            "Something went wrong",
        });
      });
  };

  const deleteMotherSet = (id) => {
    confirm({
      title: "Do you want to delete these items?",
      icon: <QuestionCircleOutlined />,
      okText: "Delete",
      cancelText: "No",
      okType: "danger",
      onOk() {
        axiosInstance
          .delete(`/set/mother/${id}/`)
          .then((res) =>
            setMotherset(motherset.filter((setID) => setID.id !== id))
          );
      },
      onCancel() {},
    });
  };
  const editMotherset = (set) => {
    const initialValues = {
      mother1: set.name,
      mother_des1: set.description,
      id: set.id,
    };
    success({
      title: `Edit ${set.name}`,
      icon: <Fragment />,

      closable: true,
      okButtonProps: { style: { display: "none" } },
      content: (
        <Form
          initialValues={initialValues}
          preserve={false}
          name="basic2"
          onFinish={updateMotherSet}
        >
          <Form.Item
            name="mother1"
            rules={[
              {
                required: true,
                message: "Please input Name",
              },
            ]}
          >
            <Input placeholder={set.name} className="form-control" />
          </Form.Item>
          <Form.Item name="id" style={{ display: "none" }}>
            <InputNumber />
          </Form.Item>

          <Form.Item name="mother_des1">
            <Input.TextArea
              placeholder={set.description}
              className="form-control"
            />
          </Form.Item>

          <Form.Item>
            <Button
              className="btn btn-primary"
              type="primary"
              htmlType="submit"
            >
              Submit
            </Button>
          </Form.Item>
        </Form>
      ),
    });
  };

  const selectSet = (event) => {
    setSelMSetID(0);
    const id = event.currentTarget.getAttribute("data-motherid");
    setSelMSetID(id);

    $("tr.motherset.table-active").removeClass("table-active");
    event.currentTarget.classList.add("table-active");
  };
  return (
    <Fragment>
      <div className="mt-5 pt-32pt pt-sm-64pt ">
        <div className="container page__container d-flex flex-column flex-md-row align-items-center text-center text-sm-left">
          <div className="flex d-flex flex-column flex-sm-row align-items-center mb-24pt mb-md-0">
            <div className="mb-24pt mb-sm-0 mr-sm-24pt">
              <h2 className="mb-0">MotherSet</h2>

              <ol className="breadcrumb p-0 m-0">
                <li className="breadcrumb-item">
                  <a href="index.html">Home</a>
                </li>

                <li className="breadcrumb-item active">MotherSet</li>
              </ol>
            </div>
          </div>
        </div>
      </div>

      <div className="container page__container">
        <div className="page-section">
          <div className="row mb-lg-16pt">
            <div className="col-lg-6 mb-8pt mb-sm-0">
              <div className="page-separator">
                <div className="page-separator__text">All MotherSets</div>
              </div>
              <div className="card">
                <div className="card-header d-flex align-items-center">
                  <div className="h2 mb-0 mr-3">{motherset.length}</div>
                  <div className="flex">
                    <p className="card-title">MotherSet</p>
                    <p className="card-subtitle text-50"></p>
                  </div>
                </div>
                <div className="card-body p-24pt">
                  <div className="chart" style={{ height: "max-content" }}>
                    <div className="form-group">
                      <Form form={form} name="basic" onFinish={addMotherSet}>
                        <Form.Item
                          name="mother"
                          rules={[
                            {
                              required: true,
                              message: "Please input Name",
                            },
                          ]}
                        >
                          <Input
                            placeholder="MotherSet Name"
                            className="form-control"
                          />
                        </Form.Item>

                        <Form.Item name="mother_des">
                          <Input.TextArea
                            placeholder="Description"
                            className="form-control"
                          />
                        </Form.Item>

                        <Form.Item>
                          <Button
                            className="btn btn-primary"
                            type="primary"
                            htmlType="submit"
                          >
                            Submit
                          </Button>
                        </Form.Item>
                      </Form>
                    </div>

                    <div className="table-responsive">
                      <div className="card-header"></div>

                      <table className="table mb-0 thead-border-top-0 table-nowrap">
                        <tbody className="list" id="search">
                          {motherset.map((set) => (
                            <tr
                              key={set.id}
                              className="motherset"
                              data-motherid={set.id}
                              onClick={selectSet}
                            >
                              <td className="col col-md-8">
                                <div className="d-flex flex-column">
                                  <p className="mb-0 p-3">
                                    <strong className=" js-lists-values-name">
                                      {set.name}
                                    </strong>
                                  </p>
                                </div>
                              </td>

                              <td className="col">
                                <Button
                                  className="chip chip-outline-primary"
                                  onClick={() => editMotherset(set)}
                                >
                                  Edit
                                </Button>
                                <Button
                                  className="chip chip-outline-danger"
                                  onClick={() => deleteMotherSet(set.id)}
                                >
                                  Delete
                                </Button>
                              </td>
                            </tr>
                          ))}
                        </tbody>
                      </table>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            {selMSetID ? <Subject motherID={selMSetID} /> : null}
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default MotherSet;
