import React, { useState, useEffect, Fragment } from "react";
import { Button, Form, Input, Modal, InputNumber } from "antd";
import axiosInstance from "../../../actions/axios";
import $ from "jquery";
import { QuestionCircleOutlined } from "@ant-design/icons";
import SubTopic from "./Subtopic";

const Topic = ({ subjectID }) => {
  const [ownTopics, setOwnTopics] = useState([]);
  const [topicID, setTopicID] = useState(0);

  const [formTopics] = Form.useForm();

  useEffect(() => {
    setTopicID(0);
    setOwnTopics([]);
    axiosInstance.get(`/set/topic/?subject=${subjectID}`).then((res) => {
      setOwnTopics(res.data);
    });
  }, [subjectID, setOwnTopics]);

  const selectTopic = (event) => {
    setTopicID(0);
    const id = event.currentTarget.getAttribute("data-topicid");
    setTopicID(id);

    $("tr.topic-class.table-active").removeClass("table-active");
    event.currentTarget.classList.add("table-active");
  };
  const addTopic = (e) => {
    const subjectBody = JSON.stringify({
      name: e.topic_name,
      subject: subjectID,
    });

    axiosInstance
      .post("/set/topic/", subjectBody)
      .then((res) => {
        setOwnTopics([...ownTopics, res.data]);
        formTopics.resetFields();
        Modal.success({
          title: "Success",
          content: <h5>Name: {res.data.name}</h5>,
        });
      })
      .catch(() => {
        Modal.error({
          title: "Error",
          content: "Something Went wrong, Refresh the Page and try again",
        });
      });
  };
  const deleteTopic = (id) => {
    Modal.confirm({
      title: "Do you want to delete these items?",
      icon: <QuestionCircleOutlined />,
      okText: "Delete",
      cancelText: "No",
      okType: "danger",
      onOk() {
        axiosInstance
          .delete(`/set/topic/${id}/`)
          .then((res) =>
            setOwnTopics(ownTopics.filter((setID) => setID.id !== id))
          );
      },
      onCancel() {},
    });
  };
  const editTopic = (set) => {
    const initialValues = {
      topic1: set.name,
      id: set.id,
    };
    Modal.success({
      title: `Edit ${set.name}`,
      icon: <Fragment />,

      closable: true,
      okButtonProps: { style: { display: "none" } },
      content: (
        <Form
          initialValues={initialValues}
          preserve={false}
          name="topicEdit"
          onFinish={updateTopic}
        >
          <Form.Item
            name="topic1"
            rules={[
              {
                required: true,
                message: "Please input Name",
              },
            ]}
          >
            <Input placeholder={set.name} className="form-control" />
          </Form.Item>
          <Form.Item name="id" style={{ display: "none" }}>
            <InputNumber />
          </Form.Item>

          <Form.Item>
            <Button
              className="btn btn-primary"
              type="primary"
              htmlType="submit"
            >
              Update
            </Button>
          </Form.Item>
        </Form>
      ),
    });
  };
  const updateTopic = (val) => {
    const topicBody = JSON.stringify({
      name: val.topic1,
    });

    axiosInstance
      .patch(`/set/topic/${val.id}/`, topicBody)
      .then((res) => {
        const objIndex = ownTopics.findIndex((obj) => obj.id === res.data.id);
        ownTopics[objIndex] = res.data;
        setOwnTopics([...ownTopics]);
        Modal.success({
          title: "Success",
          content: (
            <Fragment>
              <h5>Name: {res.data.name}</h5>
            </Fragment>
          ),
        });
        setTimeout(() => Modal.destroyAll(), 1000);
      })
      .catch(() => {
        Modal.error({
          title: "Error",
          content: "Something Went wrong, Refresh the Page and try again",
        });
      });
  };

  return (
    <Fragment>
      <div className="col-lg-6 mb-8pt mb-sm-0">
        <div className="page-separator">
          <div className="page-separator__text">
            Topics under Selected Subject
          </div>
        </div>
        <div className="card">
          <div className="card-header d-flex align-items-center">
            <div className="h2 mb-0 mr-3">{ownTopics.length}</div>
            <div className="flex">
              <p className="card-title">Topics</p>
              <p className="card-subtitle text-50"></p>
            </div>
          </div>
          <div className="card-body p-24pt">
            <div className="chart" style={{ height: "max-content" }}>
              <Form form={formTopics} onFinish={addTopic}>
                <div className="search-form">
                  <Form.Item name="topic_name" noStyle>
                    <Input
                      type="text"
                      className="form-control"
                      placeholder="Topic Name"
                    />
                  </Form.Item>
                  <Button className="btn" htmlType="submit">
                    ADD
                  </Button>
                </div>
              </Form>

              <div className="table-responsive">
                <div className="card-header"></div>

                <table className="table mb-0 thead-border-top-0 table-nowrap">
                  <tbody className="list" id="search">
                    {ownTopics.map((topic) => (
                      <tr
                        key={topic.id}
                        data-topicid={topic.id}
                        className="topic-class"
                        onClick={selectTopic}
                      >
                        <td>
                          <div className="d-flex flex-column">
                            <p className="mb-0">
                              <strong className="js-lists-values-name">
                                {topic.name}
                              </strong>
                            </p>
                          </div>
                        </td>

                        <td>
                          <Button
                            className="chip chip-outline-primary"
                            onClick={() => editTopic(topic)}
                          >
                            Edit
                          </Button>
                          <Button
                            className="chip chip-outline-danger"
                            onClick={() => deleteTopic(topic.id)}
                          >
                            Delete
                          </Button>
                        </td>
                      </tr>
                    ))}
                    {ownTopics.length ? null : (
                      <tr>
                        <td>
                          <div className="d-flex flex-column">
                            <p className="mb-0">
                              <strong className="js-lists-values-name">
                                Empty
                              </strong>
                            </p>
                          </div>
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
      {topicID ? <SubTopic topicID={topicID} /> : null}
    </Fragment>
  );
};

export default Topic;
