import React, { useState, useEffect, Fragment } from "react";
import { Button, Form, Input, Modal, InputNumber } from "antd";
import axiosInstance from "../../../actions/axios";
import { QuestionCircleOutlined } from "@ant-design/icons";

const SubTopic = ({ topicID }) => {
  const [ownSubTopics, setOwnSubTopics] = useState([]);

  const [formSubTopics] = Form.useForm();

  useEffect(() => {
    setOwnSubTopics([]);
    axiosInstance.get(`/set/subtopic/?topic=${topicID}`).then((res) => {
      setOwnSubTopics(res.data);
    });
  }, [topicID, setOwnSubTopics]);

  const addSubTopic = (e) => {
    const subTopicBody = JSON.stringify({
      name: e.subtopic_name,
      topic: topicID,
    });

    axiosInstance
      .post("/set/subtopic/", subTopicBody)
      .then((res) => {
        setOwnSubTopics([...ownSubTopics, res.data]);
        formSubTopics.resetFields();
        Modal.success({
          title: "Success",
          content: <h5>Name: {res.data.name}</h5>,
        });
      })
      .catch(() => {
        Modal.error({
          title: "Error",
          content: "Something Went wrong, Refresh the Page and try again",
        });
      });
  };
  const deleteSubTopic = (id) => {
    Modal.confirm({
      title: "Do you want to delete these items?",
      icon: <QuestionCircleOutlined />,
      okText: "Delete",
      cancelText: "No",
      okType: "danger",
      onOk() {
        axiosInstance
          .delete(`/set/subtopic/${id}/`)
          .then((res) =>
            setOwnSubTopics(ownSubTopics.filter((setID) => setID.id !== id))
          );
      },
      onCancel() {},
    });
  };
  const editSubTopic = (set) => {
    const initialValues = {
      subtopic1: set.name,
      id: set.id,
    };
    Modal.success({
      title: `Edit ${set.name}`,
      icon: <Fragment />,

      closable: true,
      okButtonProps: { style: { display: "none" } },
      content: (
        <Form
          initialValues={initialValues}
          preserve={false}
          name="subtopicEdit"
          onFinish={updateSubTopic}
        >
          <Form.Item
            name="subtopic1"
            rules={[
              {
                required: true,
                message: "Please input Name",
              },
            ]}
          >
            <Input placeholder={set.name} className="form-control" />
          </Form.Item>
          <Form.Item name="id" style={{ display: "none" }}>
            <InputNumber />
          </Form.Item>

          <Form.Item>
            <Button
              className="btn btn-primary"
              type="primary"
              htmlType="submit"
            >
              Update
            </Button>
          </Form.Item>
        </Form>
      ),
    });
  };
  const updateSubTopic = (val) => {
    const subtopicBody = JSON.stringify({
      name: val.subtopic1,
    });

    axiosInstance
      .patch(`/set/subtopic/${val.id}/`, subtopicBody)
      .then((res) => {
        const objIndex = ownSubTopics.findIndex(
          (obj) => obj.id === res.data.id
        );
        ownSubTopics[objIndex] = res.data;
        setOwnSubTopics([...ownSubTopics]);
        Modal.success({
          title: "Success",
          content: (
            <Fragment>
              <h5>Name: {res.data.name}</h5>
            </Fragment>
          ),
        });
        setTimeout(() => Modal.destroyAll(), 1000);
      })
      .catch(() => {
        Modal.error({
          title: "Error",
          content: "Something Went wrong, Refresh the Page and try again",
        });
      });
  };

  return (
    <Fragment>
      <div className="col-lg-6 mb-8pt mb-sm-0">
        <div className="page-separator">
          <div className="page-separator__text">
            SubTopics under Selected Topic
          </div>
        </div>
        <div className="card">
          <div className="card-header d-flex align-items-center">
            <div className="h2 mb-0 mr-3">{ownSubTopics.length}</div>
            <div className="flex">
              <p className="card-title">Topics</p>
              <p className="card-subtitle text-50"></p>
            </div>
          </div>
          <div className="card-body p-24pt">
            <div className="chart" style={{ height: "max-content" }}>
              <Form form={formSubTopics} onFinish={addSubTopic}>
                <div className="search-form">
                  <Form.Item name="subtopic_name" noStyle>
                    <Input
                      type="text"
                      className="form-control"
                      placeholder="Topic Name"
                    />
                  </Form.Item>
                  <Button className="btn" htmlType="submit">
                    ADD
                  </Button>
                </div>
              </Form>

              <div className="table-responsive">
                <div className="card-header"></div>

                <table className="table mb-0 thead-border-top-0 table-nowrap">
                  <tbody className="list" id="search">
                    {ownSubTopics.map((subtopic) => (
                      <tr key={subtopic.id}>
                        <td>
                          <div className="d-flex flex-column">
                            <p className="mb-0">
                              <strong className="js-lists-values-name">
                                {subtopic.name}
                              </strong>
                            </p>
                          </div>
                        </td>

                        <td>
                          <Button
                            className="chip chip-outline-primary"
                            onClick={() => editSubTopic(subtopic)}
                          >
                            Edit
                          </Button>
                          <Button
                            className="chip chip-outline-danger"
                            onClick={() => deleteSubTopic(subtopic.id)}
                          >
                            Delete
                          </Button>
                        </td>
                      </tr>
                    ))}
                    {ownSubTopics.length ? null : (
                      <tr>
                        <td>
                          <div className="d-flex flex-column">
                            <p className="mb-0">
                              <strong className="js-lists-values-name">
                                Empty
                              </strong>
                            </p>
                          </div>
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default SubTopic;
