import React, { Fragment, useState, useEffect } from "react";
import { Modal, Button, Form, Input, InputNumber } from "antd";
import Topic from "./Topic";
import $ from "jquery";
import axiosInstance from "../../../actions/axios";
import { QuestionCircleOutlined } from "@ant-design/icons";

const Subject = ({ motherID }) => {
  const [ownSubjects, setOwnSubjects] = useState([]);
  const [subjectID, setSubjectID] = useState(0);

  const [formSubjects] = Form.useForm();

  useEffect(() => {
    setOwnSubjects([]);
    setSubjectID(0);
    axiosInstance.get(`/set/subject/?motherset=${motherID}`).then((res) => {
      setOwnSubjects(res.data);
    });
  }, [motherID, setOwnSubjects]);

  const selectSubject = (event) => {
    setSubjectID(0);
    const id = event.currentTarget.getAttribute("data-subjectid");
    setSubjectID(id);

    $("tr.subject-class.table-active").removeClass("table-active");
    event.currentTarget.classList.add("table-active");
  };
  const addSubject = (e) => {
    const subjectBody = JSON.stringify({
      name: e.subject_name,
      motherset: motherID,
    });

    axiosInstance
      .post("/set/subject/", subjectBody)
      .then((res) => {
        setOwnSubjects([...ownSubjects, res.data]);
        formSubjects.resetFields();
        Modal.success({
          title: "Success",
          content: <h5>Name: {res.data.name}</h5>,
        });
      })
      .catch(() => {
        Modal.error({
          title: "Error",
          content: "Something Went wrong, Refresh the Page and try again",
        });
      });
  };
  const deleteSubject = (id) => {
    Modal.confirm({
      title: "Do you want to delete these items?",
      icon: <QuestionCircleOutlined />,
      okText: "Delete",
      cancelText: "No",
      okType: "danger",
      onOk() {
        axiosInstance
          .delete(`/set/subject/${id}/`)
          .then((res) =>
            setOwnSubjects(ownSubjects.filter((setID) => setID.id !== id))
          );
      },
      onCancel() {},
    });
  };
  const editSubject = (set) => {
    const initialValues = {
      subject1: set.name,
      id: set.id,
    };
    Modal.success({
      title: `Edit ${set.name}`,
      icon: <Fragment />,

      closable: true,
      okButtonProps: { style: { display: "none" } },
      content: (
        <Form
          initialValues={initialValues}
          preserve={false}
          name="basic2"
          onFinish={updateSubject}
        >
          <Form.Item
            name="subject1"
            rules={[
              {
                required: true,
                message: "Please input Name",
              },
            ]}
          >
            <Input placeholder={set.name} className="form-control" />
          </Form.Item>
          <Form.Item name="id" style={{ display: "none" }}>
            <InputNumber />
          </Form.Item>

          <Form.Item>
            <Button
              className="btn btn-primary"
              type="primary"
              htmlType="submit"
            >
              Update
            </Button>
          </Form.Item>
        </Form>
      ),
    });
  };
  const updateSubject = (val) => {
    const subjectBody = JSON.stringify({
      name: val.subject1,
    });

    axiosInstance
      .patch(`/set/subject/${val.id}/`, subjectBody)
      .then((res) => {
        const objIndex = ownSubjects.findIndex((obj) => obj.id === res.data.id);
        ownSubjects[objIndex] = res.data;
        setOwnSubjects([...ownSubjects]);
        Modal.success({
          title: "Success",
          content: (
            <Fragment>
              <h5>Name: {res.data.name}</h5>
            </Fragment>
          ),
        });
        setTimeout(() => Modal.destroyAll(), 1000);
      })
      .catch(() => {
        Modal.error({
          title: "Error",
          content: "Something Went wrong, Refresh the Page and try again",
        });
      });
  };

  return (
    <Fragment>
      <div className="col-lg-6 mb-8pt mb-sm-0">
        <div className="page-separator">
          <div className="page-separator__text">
            Subjects under Selected Motherset
          </div>
        </div>
        <div className="card">
          <div className="card-header d-flex align-items-center">
            <div className="h2 mb-0 mr-3">{ownSubjects.length}</div>
            <div className="flex">
              <p className="card-title">Subjects</p>
              <p className="card-subtitle text-50"></p>
            </div>
          </div>
          <div className="card-body p-24pt">
            <div className="chart" style={{ height: "max-content" }}>
              <Form form={formSubjects} onFinish={addSubject}>
                <div className="search-form">
                  <Form.Item name="subject_name" noStyle>
                    <Input
                      type="text"
                      className="form-control"
                      placeholder="Subject Name"
                      id="searchSample03"
                    />
                  </Form.Item>
                  <Button className="btn" htmlType="submit">
                    ADD
                  </Button>
                </div>
              </Form>

              <div className="table-responsive">
                <div className="card-header"></div>

                <table className="table mb-0 thead-border-top-0 table-nowrap">
                  <tbody className="list" id="search">
                    {ownSubjects.map((sub) => (
                      <tr
                        key={sub.id}
                        data-subjectid={sub.id}
                        className="subject-class"
                        onClick={selectSubject}
                      >
                        <td>
                          <div className="d-flex flex-column">
                            <p className="mb-0">
                              <strong className="js-lists-values-name">
                                {sub.name}
                              </strong>
                            </p>
                          </div>
                        </td>

                        <td>
                          <Button
                            className="chip chip-outline-primary"
                            onClick={() => editSubject(sub)}
                          >
                            Edit
                          </Button>
                          <Button
                            className="chip chip-outline-danger"
                            onClick={() => deleteSubject(sub.id)}
                          >
                            Delete
                          </Button>
                        </td>
                      </tr>
                    ))}
                    {ownSubjects.length ? null : (
                      <tr>
                        <td>
                          <div className="d-flex flex-column">
                            <p className="mb-0">
                              <strong className="js-lists-values-name">
                                Empty
                              </strong>
                            </p>
                          </div>
                        </td>
                      </tr>
                    )}
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>

      {subjectID ? <Topic subjectID={subjectID} /> : null}
    </Fragment>
  );
};

export default Subject;
